import React, { Component } from "react";
import { Button, Card, CardBody, CardHeader, CardText, Col, Collapse, Row } from 'reactstrap';
import Header from '../components/Header';
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faChessBoard, faLink } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import CodeQuiz from '../assets/og-code-quiz.png';
import Scheduler from '../assets/og-scheduler.png';
import Weather from '../assets/og-weather-dash.png';
import Radio from '../assets/og-radio-finder.png';
import Notes from '../assets/og-note-taker.png';
import Burger from '../assets/og-burger-bar.png';
import Fitness from '../assets/og-fitness-tracker.png';
import Directory from '../assets/employee-directory.png';

class Projects extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      collapse: 0,
      projects: [
        { index: 9, name: "employee directory", img: Directory, desc: "white pages for employees", repo: "https://github.com/maphaiyarath/employee-directory", demo: "https://maphaiyarath-employeedirectory.herokuapp.com/" },
        { index: 8, name: "fitness tracker", img: Fitness, desc: "track those gains", repo: "https://github.com/maphaiyarath/fitness-tracker", demo: "https://maphaiyarath-fitness-tracker.herokuapp.com/" },
        { index: 7, name: "foodiebook", img: "image coming soon!", desc: "go ahead, rant on...", repo: "https://github.com/Corder25/Foodie-Book", demo: "https://morning-peak-44853.herokuapp.com/" },
        { index: 6, name: "burger bar", img: Burger, desc: "it's burger time!", repo: "https://github.com/maphaiyarath/burger-bar", demo: "https://maphaiyarath-burger-bar.herokuapp.com/" },
        { index: 5, name: "note taker", img: Notes, desc: "notes on notes", repo: "https://github.com/maphaiyarath/note-taker", demo: "https://maphaiyarath-note-taker.herokuapp.com/" },
        { index: 4, name: "radio finder", img: Radio, desc: "grab an iced tea and listen to some radio stations from around the U.S.", repo: "https://github.com/maphaiyarath/radio-finder", demo: "https://maphaiyarath.github.io/radio-finder/" },
        { index: 3, name: "weather dashboard", img: Weather, desc: "what's the weather?", repo: "https://github.com/maphaiyarath/weather-dashboard", demo: "https://maphaiyarath.github.io/weather-dashboard/" },
        { index: 2, name: "work day scheduler", img: Scheduler, desc: "workin' 9 to 5 ð¶", repo: "https://github.com/maphaiyarath/work-day-scheduler", demo: "https://maphaiyarath.github.io/work-day-scheduler/" },
        { index: 1, name: "code quiz", img: CodeQuiz, desc: "test your knowledge with this coding quiz challenge!", repo: "https://github.com/maphaiyarath/code-quiz", demo: "https://maphaiyarath.github.io/code-quiz/" }
      ]
    };
  }

  toggle(e) {
    let event = e.target.dataset.event;
    this.setState({
      collapse: this.state.collapse === Number(event) ? 0 : Number(event)
    });
  }

  render() {
    const { projects, collapse } = this.state;
    return (
      <div>
        <Row className="justify-content-center">
          <Col sm="12" md="10" lg="6">
            <Header title="projects"/>
            <h3 className="text-center"><FontAwesomeIcon icon={ faChessBoard } /> bootcamp <FontAwesomeIcon icon={ faChessBoard } /></h3>
            {
              projects.map((project) => {
                return (
                <Card key={ project.index }>
                  <CardHeader onClick={ this.toggle } data-event={ project.index }>
                    { project.name }
                  </CardHeader>
                  <Collapse isOpen={ collapse === project.index }>
                    <div className="text-center">
                      <CardBody>
                        <img width="100%" src={ project.img } alt={ project.name } />
                        <CardText>
                          { project.desc }
                        </CardText>
                        <br />
                        <Button style={{ borderRadius: '50%' }} outline color="secondary" href={ project.repo } target="_blank"><FontAwesomeIcon icon={ faGithub } /></Button> <Button style={{ borderRadius: '50%' }} outline color="secondary" href={ project.demo } target="_blank"><FontAwesomeIcon icon={ faLink } /></Button>
                      </CardBody>
                    </div>
                  </Collapse>
                </Card>
                )
              })
            }
          </Col>
        </Row>
      </div>
    );
  }
}

export default Projects;