import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
import { faGithub, faLinkedinIn } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link, animateScroll as scroll } from "react-scroll";

const NavBar = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  const scrollToTop = () => {
    scroll.scrollToTop();
  };

  return (
      <Navbar color="light" light expand="lg">
        <NavbarBrand href="/" onClick={ scrollToTop }>michelle aphaiyarath</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink>
                <Link
                  to="about"
                  spy={true}
                  smooth={true}
                  duration={500}
                >
                  about
                </Link>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink>
                <Link
                  to="projects"
                  spy={true}
                  smooth={true}
                  duration={500}
                >
                  projects
                </Link>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink>
                <Link
                  to="contact"
                  spy={true}
                  smooth={true}
                  duration={500}
                >
                  contact
                </Link>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://www.linkedin.com/in/michelle-aphaiyarath"><FontAwesomeIcon icon={ faLinkedinIn } /></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://www.github.com/maphaiyarath"><FontAwesomeIcon icon={ faGithub } /></NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
  );
}

export default NavBar;